@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap);
:root {
  --orange: rgb(230, 0, 0);
  --gray: rgb(196, 196, 196);
  --purple:rgb(90, 45, 255);
  --cyan:rgb(4, 169, 219);
}
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f5f5f5!important;
}
select{
  border-radius: 0!important;
  border-bottom: 2px solid rgb(230, 0, 0)!important;
  border-bottom: 2px solid var(--orange)!important;
}
label{
  font-size: 12px;
}
select:focus,select:active{
  outline: none!important;
  border-color: rgb(230, 0, 0)!important;
  border-color: var(--orange)!important;
  box-shadow: none!important;
}
button.compact{
  padding-top: 3px;
  padding-bottom: 3px;
  font-size: 14px;
}
.modal-header{
  border-bottom: none!important;
}

.itemProducto{
  margin-top: 8px;
}
.itemProducto div:only-child{
  border: 1px solid #dee2e6;
}
.itemProducto div:hover{
  color: black;
}

.itemProducto div{
  color: rgb(196, 196, 196);
  color: var(--gray);
  transition: .5s;
  padding-top:10px;
  padding-bottom:10px;
  border-top: 1px solid #dee2e6;
  border-bottom: 1px solid transparent;
}
.itemProducto div:last-child{
  border-bottom: 1px solid #dee2e6;
}
.navbar{
  background-color: white!important;
  border: #dee2e6 solid 1px;
  padding: 0 1rem!important;
  height: 61px;
}
.nav-link,.navbar-brand{
  font-weight: 500;
  transition: .3s ease all;
}
.nav-link.active{
  border-bottom: 3px solid rgb(230, 0, 0);
  border-bottom: 3px solid var(--orange);
}
.nav-link{
  padding: 1rem 0.5rem!important;
  border-bottom: 3px solid white;
}
.nav-link:not(.active):hover{
  border-bottom: 3px solid black;
}
.search-field{
  height: 100%;
  outline:none;
  border: none;
  text-align: center;
  width: 100%;
}
button:focus{
  outline: none!important;
}
tr{
  transition: .5s linear;
}

thead{
  background: white;
  border-bottom: 3px solid rgb(230, 0, 0);
  border-bottom: 3px solid var(--orange);
}
th,td{
  border: none!important;
}
th{
  font-size: 14px;
  font-weight: 5  00;
}
td{
  color: rgba(0,0,0,.5);
  font-weight: 400;
}
tr{
  background: white;
  transition: .3s;
}
tr:hover > td{
  color: black;
}
.td-tag{
  display: none;
}

@media only screen and (max-width: 760px),
  (min-device-width: 768px) and (max-device-width: 1024px){
.navbar{
 
  padding: 0!important;
 
}
.navbar-collapse{
  z-index: 1;
    background: white;
    border-bottom: 1px solid #dee2e6;
}
.navbar-toggler{
  margin-top: 10px;
    margin-bottom: 10px;
    margin-right: 0.5rem;
}
.navbar-toggler:active{
  outline-color: rgb(230, 0, 0);
  outline-color: var(--orange);
}
.navbar-toggler:focus{
  outline: none;
}
.nav-link.active,.nav-link{
  border-bottom: none;
}
.nav-link:not(.active):hover{
  border-bottom: none;
}
.navbar-brand{
  padding-left: 0.5rem;
}


  table,
  tbody,
  th,
  td,
  tr {
    display: block;
  }
  thead{
    display: none;
  }
  tr{
    margin-bottom: 15px;
    border: #dee2e6 solid 1px;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 0.5rem;
    border-left: 3px solid rgb(230, 0, 0);
    border-left: 3px solid var(--orange);
  }
  table{
    border: none!important;
  }
  .td-tag{
    display: block;
    margin-bottom: -3px;
    font-size: 14px;
    color: #131313;
  }
  td{
    padding-top: 0.2rem!important;
    padding-bottom: 0.2rem!important;
    font-size: 15px;
  }
  
}
.borderless{
  background:transparent;
  border: none;
  padding:0;
  color: rgb(196, 196, 196);
  color: var(--gray);
}
.borderless:hover{
  background:none;
  color:black;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.fg input{
    width: 250px;
    border: none;
    border-bottom: 2px solid rgb(196, 196, 196);
    outline: none;
    margin: 5px;
    transition: 0.2s ease all;
}
.fg label{
    margin-bottom: 0!important;
}
button{
    outline: none;
    background: white;
    border: 1px solid rgb(196, 196, 196);
    border-bottom: 2px solid var(--orange);
    padding: 10px 15px;
    transition: 0.2s ease all;
}
button:hover{
    border-color: var(--orange);
    background: var(--orange);
    color: white;
}
.fg{
    display: grid;
 
}
.fg > label{
    transform: translate(0%,-60%);
    z-index: 0;
    transition: 0.2s ease all;
}
.fg > input{
    transform:translate(0,15px);
}
.fg > input{
    z-index: 1;
    background: transparent;
}
.fg > input:not(:-ms-input-placeholder) ~ label{
    transform: translate(0%,-35px);
    font-size: 12px;
}
.fg >input:focus ~ label,
.fg > input:not(:placeholder-shown) ~ label{
    transform: translate(0%,-35px);
    font-size: 12px;
}
.fg > input:focus{
    border-bottom: 2px solid var(--orange);
}
.card.login{
    position: absolute!important;
    padding: 20px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-top: 2px solid var(--orange)!important;
    border-radius: 0!important;
    min-height: 290px;
}
